
footer {
    left: 0;
    bottom: 0;
    color: var(--neutral-white);
    display: flex;
    justify-content: center;
    gap: 7rem;
    padding: 9rem 0;
    background: var(--primary-dark-blue);
    font-size: 1rem;
}

footer > div {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: space-between;
}

.footer-nav-1 > div, .footer-nav-2 > div {
    transition: 100ms ease-in-out;
}

.footer-nav-1 > div:hover, .footer-nav-2 > div:hover {
    cursor: pointer;
    color: var(--neutral-grayish-blue)
}

.socials {
    display: flex;
    gap: 1rem;
}

.socials > img {
    transition: 20ms ease-in-out;
}

.footer-socials > div:nth-of-type(1) {
    font-weight: 700;
    font-size: 1.3rem;
    display: flex;
    align-items: center;
}

.footer-socials > div:nth-of-type(1) > span {
    position: relative;
    left: -6.2rem;
}

.socials > img:hover {
    cursor: pointer;
    filter: brightness(0) saturate(100%) invert(72%) sepia(14%) saturate(3289%) hue-rotate(154deg) brightness(90%) contrast(89%);
} 
    
